import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  Collapse,
  ListItemText,
} from '@material-ui/core';
import clsx from 'clsx';
import { set, get } from 'idb-keyval';
import { useSelector } from 'react-redux';

import { AppState } from '../../../state';
import { AnimatedExpandedIcon } from '../common/AnimatedExpandedIcon';
import { ChapterFavList } from './chapter-fav-list';
import { KuralFavList } from './kural-fav-list';

const useStyles = makeStyles((theme) => ({
  root: {},
  collapsableGroup: {},
  list: {
    '& .sub-header': {
      textAlign: 'left',
      color: 'brown',
      lineHeight: '2rem',
      padding: `0px 0 0 ${theme.spacing(0.5)}px`,
    },
  },
  listItemTitle: {
    '& .toggle-icon-container': {
      marginLeft: theme.spacing(1.5),
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
  },
  favCount: {
    fontSize: '0.8rem',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
}));

type FavoritesViewerProps = {
  className?: string;
  expanded?: boolean;
};

const IDB_FAV_TOGGLE_STATE = 'IDB_FAV_TOGGLE';
type VisibilityState = {
  kural: boolean;
  chapter: boolean;
};

export const FavoritesViewer: React.FC<FavoritesViewerProps> = (
  props: FavoritesViewerProps
) => {
  const classes = useStyles();
  const favorites = useSelector((st: AppState) => st.user.favorites);
  const filterFav = (category: 'kural' | 'chapter') =>
    (favorites || [])
      .filter((it) => it.category === category)
      .map((it) => it.id);
  const KURAL_IDS = filterFav('kural');
  const CHAPTER_IDS = filterFav('chapter');

  const [visiblity, setVisiblity] = React.useState<VisibilityState>(
    props.expanded
      ? { kural: true, chapter: true }
      : { kural: false, chapter: false }
  );

  React.useEffect(() => {
    // runs only once
    get(IDB_FAV_TOGGLE_STATE).then((st) => {
      setVisiblity(JSON.parse(st as string) as VisibilityState);
    });
  }, []);

  React.useEffect(() => {
    (async () => {
      await set(IDB_FAV_TOGGLE_STATE, JSON.stringify(visiblity));
    })();
  }, [visiblity]);

  const noRecordsFound = <h1>None Found</h1>;

  const GroupTitleMaker = (
    isVisible: boolean,
    onClick: () => void,
    title: React.ReactNode,
    favCount: React.ReactNode,
    collapseContent?: React.ReactNode
  ) => (
    <>
      <ListItem
        selected={isVisible}
        className={classes.listItemTitle}
        button
        onClick={onClick}>
        <ListItemText inset={true} primary={title} />
        {!isVisible && <span className={classes.favCount}>{favCount}</span>}
        <div className={`toggle-icon-container`}>
          <AnimatedExpandedIcon isExpanded={isVisible} />
        </div>
      </ListItem>
      <Collapse
        in={isVisible}
        style={{ paddingBottom: isVisible ? 8 : 0 }}
        className={classes.collapsableGroup}
        timeout='auto'>
        {collapseContent || noRecordsFound}
      </Collapse>
    </>
  );
  return (
    <div className={clsx(props.className, classes.root)}>
      <List className={classes.list} disablePadding>
        {GroupTitleMaker(
          visiblity.kural,
          () => {
            setVisiblity({ ...visiblity, kural: !visiblity.kural });
          },
          'Kurals',
          KURAL_IDS.length ? KURAL_IDS.length : null,
          KURAL_IDS.length && <KuralFavList kuralIds={KURAL_IDS} />
        )}
        {GroupTitleMaker(
          visiblity.chapter,
          () => {
            setVisiblity({ ...visiblity, chapter: !visiblity.chapter });
          },
          'Chapters',
          CHAPTER_IDS.length ? CHAPTER_IDS.length : null,
          CHAPTER_IDS.length && <ChapterFavList chapterIds={CHAPTER_IDS} />
        )}
      </List>
    </div>
  );
};
