import React from 'react';

import { makeStyles } from '@material-ui/core';
import { useSelector } from 'react-redux';

import { BaseType, findCategoryRouteMapString } from '../../../lib/api';
import { useBaseItem, useNavigation } from '../../../lib/hooks';
import { ROUTE_CONSTANTS } from '../../../routes';
import { AppState } from '../../../state';
import ListView from '../common/ListView';

const useStyles = makeStyles((theme) => ({
  chapFavorites: {
    padding: 0,
    '& li': {
      paddingLeft: theme.spacing(0.5),
      paddingRight: theme.spacing(0.5),
    },
  },
}));

export interface ChapterFavListProps {
  chapterIds: number[];
}

export const ChapterFavList: React.FC<ChapterFavListProps> = (
  props: ChapterFavListProps
) => {
  const classes = useStyles();
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const [getChapter] = useBaseItem('chapter');
  const [navigateTo] = useNavigation();

  const onChapterSelected = (item: BaseType): void => {
    if (INDEX_MAP) {
      navigateTo(
        `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP, {
          chapter: item.id,
        })}`
      )();
    }
  };

  return (
    <>
      {props.chapterIds.length && (
        <ListView
          className={classes.chapFavorites}
          items={
            props.chapterIds
              .sort()
              .map((ch) => getChapter(ch))
              .filter((ch) => ch !== null) as BaseType[]
          }
          onItemSelected={onChapterSelected}
        />
      )}
    </>
  );
};
