import React from 'react';

import { List, ListSubheader, makeStyles } from '@material-ui/core';
import { useSelector } from 'react-redux';

import {
  BaseType,
  findCategoryRouteMapString,
  findChapterNoFromKuralNo,
} from '../../../lib/api';
import { useBaseItem, useNavigation } from '../../../lib/hooks';
import { ROUTE_CONSTANTS } from '../../../routes';
import { AppState } from '../../../state';
import KuralCard from './../kural/KuralCard';

const useStyles = makeStyles((theme) => ({
  kural: {
    padding: `0 0 0 ${theme.spacing(1)}px`,
    '&:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },
  },
}));

export interface KuralFavListProps {
  kuralIds: number[];
}

export const KuralFavList: React.FC<KuralFavListProps> = (
  props: KuralFavListProps
) => {
  const classes = useStyles();
  const KURALS = useSelector((st: AppState) => st.kuralIndexMap.kurals);
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const [getChapter] = useBaseItem('chapter');
  const [navigateTo] = useNavigation();
  const [chapterKuralMap, setchapterKuralMap] = React.useState<
    Record<number, BaseType[]>
  >({});

  const onChapterSelected = (item: BaseType): void => {
    if (INDEX_MAP) {
      navigateTo(
        `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP, {
          chapter: item.id,
        })}`
      )();
    }
  };

  const onKuralSelected = (item: BaseType): void => {
    if (INDEX_MAP) {
      navigateTo(
        `${ROUTE_CONSTANTS.LIST}/${findCategoryRouteMapString(INDEX_MAP, {
          kural: item.id,
        })}`
      )();
    }
  };

  React.useEffect(() => {
    const chapMap = props.kuralIds.reduce((chapMap, kId) => {
      const chap = findChapterNoFromKuralNo(kId);
      if (KURALS[kId] !== undefined) {
        return {
          ...chapMap,
          [chap]: [...(chapMap[chap] || []), KURALS[kId]],
        };
      }
      return chapMap;
    }, {} as Record<number, BaseType[]>);
    setchapterKuralMap(chapMap);
  }, [props.kuralIds, KURALS]);

  return (
    <>
      {Object.keys(chapterKuralMap).map((chapId) => {
        const chapterInfo = getChapter(parseInt(chapId));
        return (
          <List
            component='div'
            className='sub-list'
            key={chapId}
            disablePadding>
            {chapterInfo && (
              <ListSubheader
                className='sub-header'
                disableSticky
                onClick={(): void => onChapterSelected(chapterInfo)}>
                {chapterInfo.value}
              </ListSubheader>
            )}

            {chapterKuralMap[parseInt(chapId)].map((item: BaseType) => (
              <KuralCard
                className={classes.kural}
                key={item.id}
                kural={item}
                onClick={onKuralSelected}
              />
            ))}
          </List>
        );
      })}
    </>
  );
};
