import React from 'react';

import { Card, CardContent, makeStyles } from '@material-ui/core';
import {
  Check,
  Favorite,
  Grade,
  SvgIconComponent,
  Whatshot,
} from '@material-ui/icons';
import clsx from 'clsx';
import { useDispatch, useSelector } from 'react-redux';
import { useFirestore, useFirestoreConnect } from 'react-redux-firebase';
import { AppState } from 'src/state';
import { applyPreference, DEFAULT_COLORS } from 'src/state/settings';

import {
  User,
  COLLECTION_NAME_MAP,
  updateUserPreference,
} from '../../../lib/api';
import { CommonComponentProps } from './../../components';

const useStyles = makeStyles((theme) => ({
  root: {},
  colorItemContainer: {
    display: 'grid',
    gridTemplateColumns: 'repeat(3, auto)',
    gridGap: '1em',
  },
  colorItem: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    alignItems: 'center',
    '& .circle': {
      height: '2.5rem',
      width: '2.5rem',
      borderRadius: '50%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      '& svg': {
        color: 'white',
      },
    },
    '& .icon': {
      marginTop: '0.5em',
    },
  },
}));

const ICON_MAP: SvgIconComponent[] = [Favorite, Grade, Whatshot];

interface ColorItemProps {
  color: string;
  icon: SvgIconComponent;
  isSelected?: boolean;
  onSelect?: () => void;
}

const ColorItem: React.FC<ColorItemProps> = ({
  isSelected = false,
  ...props
}: ColorItemProps) => {
  const classes = useStyles();
  const [itemSelected, setItemSelection] = React.useState(isSelected);
  React.useEffect(() => {
    setItemSelection(isSelected);
  }, [isSelected]);
  const onItemSelection = () => {
    setItemSelection(!itemSelected);
    props.onSelect && props.onSelect();
  };
  return (
    <div className={classes.colorItem}>
      <div
        className='circle'
        style={{ backgroundColor: props.color }}
        onClick={onItemSelection}>
        {itemSelected && <Check />}
      </div>
      <div className='icon'>
        {<props.icon style={{ color: props.color }} />}
      </div>
    </div>
  );
};

export interface ColorPickerProps extends Partial<CommonComponentProps> {
  user: User;
}

export const ColorPicker: React.FC<ColorPickerProps> = (
  props: ColorPickerProps
) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  useFirestoreConnect([COLLECTION_NAME_MAP.DISPLAY]);
  const firestore = useFirestore();
  const DISPLAY_CONFIG = useSelector(
    (st: AppState) => st.firestore.data[COLLECTION_NAME_MAP.DISPLAY]
  );
  const USER_COLOR = props.user.preference.display.color;
  const getColors = (): string[] => {
    const COLORS = [
      ...(DISPLAY_CONFIG && DISPLAY_CONFIG.CONFIG
        ? DISPLAY_CONFIG.CONFIG.colors
        : DEFAULT_COLORS),
    ];
    if (USER_COLOR && COLORS.indexOf(USER_COLOR) === -1) {
      COLORS.splice(0, 1, USER_COLOR);
    }
    return COLORS;
  };
  const updateUserDisplayColor = (color: string) => {
    const userPreference = {
      ...props.user.preference,
      display: {
        ...props.user.preference.display,
        color,
      },
    };
    dispatch(applyPreference(userPreference));
    updateUserPreference(firestore, {
      ...props.user,
      preference: userPreference,
    });
  };
  return (
    <Card variant={'outlined'} className={clsx(props.className, classes.root)}>
      <CardContent className={classes.colorItemContainer}>
        {getColors().map((c: string, i: number) => {
          return (
            <ColorItem
              key={i}
              color={c}
              icon={ICON_MAP[i]}
              isSelected={USER_COLOR === c}
              onSelect={() => updateUserDisplayColor(c)}
            />
          );
        })}
      </CardContent>
    </Card>
  );
};
