import * as React from 'react';

import { makeStyles } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { renderRoutes } from 'react-router-config';
import { HashRouter as Router } from 'react-router-dom';

import { FONT_SIZE_MAP } from './lib/api';
import { routes } from './routes';
import { thunkInitKuralIndexMapAndUserPref } from './state/kuralIndex';
import { AppState } from './state/reducers';

const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.up('sm')]: {
      fontSize: '3vw',
    },
  },
}));

const App: React.FC = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const firestore = useFirestore();
  const user = useSelector((st: AppState) => st.user);
  React.useEffect(() => {
    dispatch(thunkInitKuralIndexMapAndUserPref(firestore));
  }, [dispatch, firestore]);

  React.useEffect(() => {
    const bodyStyles = document.body.style;
    bodyStyles.setProperty(
      '--text-size',
      `${FONT_SIZE_MAP[user.preference.fontSizeType]}px`
    );
    bodyStyles.setProperty('--app-color', user.preference.display.color);
    bodyStyles.setProperty(
      '--app-bg-color',
      user.preference.display.background.color
    );
  }, [user]);
  return (
    <div
      className={classes.root}
      style={{
        fontSize: 'var(--text-size)',
        color: 'var(--app-color)',
        backgroundColor: 'var(--app-bg-color)',
      }}>
      <Router>{renderRoutes(routes)}</Router>
    </div>
  );
};

export default App;
